import React from "react";

class Weather extends React.Component {
    render() {
        const data = this.props.weatherData;
        return (
            <div className="Weather">
                <div className="Text">
                    {data.city && data.country && (
                        <>
                            <p className="Location">
                                Getting weather for {data.city}, {data.country}
                            </p>
                            <hr />
                        </>
                    )}
                    {data.condition && (
                        <>
                            <p className="Condition">
                                Condition: {data.condition}
                            </p>
                            <hr />
                        </>
                    )}
                    {data.temp && (
                        <>
                            <p className="Temp">Temperature: {data.temp}°</p>
                            <hr />
                        </>
                    )}
                    {data.humidity && (
                        <>
                            <p className="Humidity">
                                Humidity: {data.humidity}%
                            </p>
                            <hr />
                        </>
                    )}
                    {data.windspeed && data.winddir && (
                        <>
                            <p className="Wind">
                                Wind: Blowing {data.windspeed} mph at{" "}
                                {data.winddir}°
                            </p>
                            <hr />
                        </>
                    )}
                    {data.clouds && (
                        <p className="Clouds">Cloud Coverage: {data.clouds}%</p>
                    )}
                    {data.error && <p className="Error">{data.error}</p>}
                </div>
            </div>
        );
    }
}

export default Weather;
