import React from "react";

import Title from "./assets/components/Title";
import Form from "./assets/components/Form";
import Weather from "./assets/components/Weather";

import "./assets/styles/css/default.min.css";

const API_KEY = "ca21dc061cfbfac4c0dd566e7366af0b";

class App extends React.Component {
    state = {
        city: "",
        county: "",
        condition: "",
        temp: "",
        humidity: "",
        windspeed: "",
        winddir: "",
        clouds: "",
        error: ""
    };

    componentDidMount() {
        if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition(this.getWeatherLocation);
        }
    }

    getWeatherLocation = async position => {
        const lat = position.coords.latitude;
        const lon = position.coords.longitude;
        await fetch(
            `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&APPID=${API_KEY}&units=imperial`
        )
            .then(res => res.json())
            .then(data =>
                this.setState({
                    city: data.name,
                    country: data.sys.country,
                    condition: data.weather[0].description,
                    temp: data.main.temp,
                    humidity: data.main.humidity,
                    windspeed: data.wind.speed,
                    winddir: data.wind.deg,
                    clouds: data.clouds.all,
                    error: ""
                })
            )
            .catch(error =>
                this.setState({
                    city: "",
                    country: "",
                    condition: "",
                    temp: "",
                    humidity: "",
                    windspeed: "",
                    winddir: "",
                    clouds: "",
                    error: "Enter a valid location!"
                })
            );
    };

    getWeather = async e => {
        e.preventDefault();
        const location = e.target.elements.location.value;
        const country = e.target.elements.country.value;
        const call_type = isNaN(location) ? "q" : "zip";

        await fetch(
            `https://api.openweathermap.org/data/2.5/weather?${call_type}=${location},${country}&APPID=${API_KEY}&units=imperial`
        )
            .then(res => res.json())
            .then(data =>
                this.setState({
                    city: data.name,
                    country: data.sys.country,
                    condition: data.weather[0].description,
                    temp: data.main.temp,
                    humidity: data.main.humidity,
                    windspeed: data.wind.speed,
                    winddir: data.wind.deg,
                    clouds: data.clouds.all,
                    error: ""
                })
            )
            .catch(error =>
                this.setState({
                    city: "",
                    country: "",
                    condition: "",
                    temp: "",
                    humidity: "",
                    windspeed: "",
                    winddir: "",
                    clouds: "",
                    error: "Enter a valid location!"
                })
            );
    };

    render() {
        return (
            <div className="App">
                <Title />
                <Form getWeather={this.getWeather} />
                <Weather weatherData={this.state} />
            </div>
        );
    }
}

export default App;
