import React from "react";

const countryJson = require("../json/ISO3166");

class Form extends React.Component {
    render() {
        return (
            <form className="Form" onSubmit={this.props.getWeather}>
                <input
                    className="Location"
                    id="Location"
                    type="text"
                    name="location"
                    placeholder="City or Zip Code"
                />
                <select
                    className="Country"
                    name="country"
                    placeholder="Country"
                    defaultValue="US"
                >
                    {countryJson.map((country, index) => {
                        return (
                            <option value={country["alpha-2"]} key={index}>
                                {country.name}
                            </option>
                        );
                    })}
                </select>
                <input type="submit" value="Get Weather" />
            </form>
        );
    }
}

export default Form;
